html {
  scroll-behavior: smooth;
}

body {
  background-color: #faebd7;
  margin: 0;
  padding: 0;
}

.search-form {
  background-color: bisque;
  border-bottom: 1px solid #e9967a;
  justify-content: center;
  gap: 30px;
  padding: 30px;
  display: flex;
}

.search-form__input {
  border-color: #e9967a;
  border-radius: 20px;
  padding: 12px;
}

.search-form__input:active {
  border-color: #f08080;
}

.button {
  color: #fff;
  font-size: inherit;
  cursor: pointer;
  background-color: #e9967a;
  border: none;
  border-radius: 30px;
  padding: 12px 32px;
}

.button:focus, .button:hover {
  background-color: #f08080;
}

.load-more {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.load-more.is-hidden {
  opacity: 1;
  pointer-events: painted;
  visibility: visible;
}

